html, body {
  margin: 0;
  padding: 0;
  background: #F2F2F2; }

a:hover, a:active, a:visited, a:link, a:focus {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  outline: none;
  background: none;
  text-decoration: none; }

.App {
  text-align: center; }
  .App .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px; }
  .App .App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white; }
  .App .App-title {
    font-size: 1.5em; }
  .App .App-intro {
    font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.mainnavigative {
  width: 100%;
  height: 100vh;
  position: relative; }
  .mainnavigative .navigativeBox {
    width: 100%;
    height: 100%;
    padding-bottom: 70px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .mainnavigative .contentBox {
    width: 100%;
    position: fixed;
    bottom: 0;
    background: white;
    z-index: 999999999; }
    .mainnavigative .contentBox .navigative {
      display: inline-block;
      padding: 10px 0; }
      .mainnavigative .contentBox .navigative .navIcon {
        width: 27px; }
      .mainnavigative .contentBox .navigative .navTitle {
        display: block;
        font-size: 14px;
        color: #666; }
      .mainnavigative .contentBox .navigative .navigativeRed {
        color: #E52D27; }



